button {
  outline: none;
  border-radius: 3px;
  font-size: 1em;
  padding: 0.5em 0.5em;
  background: transparent;
  color: darkgrey;
  border: 2px solid darkgrey; }
  button.primary {
    color: #EF5B5B;
    border: 2px solid #EF5B5B; }
  button.secondary {
    color: #1B998B;
    border: 2px solid #1B998B; }
  button.accent {
    color: #9B5094;
    border: 2px solid #9B5094; }
  button.block {
    background-color: white;
    color: white; }
    button.block.primary {
      background-color: #EF5B5B; }
    button.block.secondary {
      background-color: #1B998B; }
    button.block.accent {
      background-color: #9B5094; }
  button.icon {
    font-size: 0.8em;
    padding: 0em 0em;
    background: transparent;
    border: none; }
  button .material-icons {
    vertical-align: bottom;
    font-size: 20px;
    margin-right: 5px; }

.nav {
